import React from 'react';
import { string, shape } from 'prop-types';

import { SignupForm } from '../SignupForm';

import './ExtraBlock.scss';

const ExtraBlock = ({ params, bg }) => {
  const style = bg && {
    backgroundImage: `url(${process.env.ASSETS_URL}${bg})`
  };

  return (
    <div className='extra-block__container' style={style}>
      <div className='extra-block__content'>
        <h2>{params && params.title}</h2>

        <p className='copytext extra-block__description'>
          {params && params.description}
        </p>

        <SignupForm
          buttonText={params && params.buttonText}
          inputPlaceholder={params && params.inputPlaceholder}
        />
      </div>
    </div>
  );
};

ExtraBlock.propTypes = {
  params: shape({
    title: string,
    description: string,
    action: string
  }),
  bg: string
};

export default ExtraBlock;



