import React from 'react';
import { string, shape } from 'prop-types';

import './Security.scss';

const SecurityBlock = ({ params }) => (
  <section className='securityBlock__container'>
    <div className='securityBlock__descriptionContainer'>
      <h2
        dangerouslySetInnerHTML={{ __html: params.title }}
      />

      <p
        className='securityBlock__description copytext-large'
        dangerouslySetInnerHTML={{ __html: params.description }}
      />
    </div>

    <div className='securityBlock__text'>
      {params.text
        .split('<br/>')
        .map((item, index) => (
          <p
            className='copytext'
            key={index}
            dangerouslySetInnerHTML={{ __html: item }}
          />
        ))}
    </div>
  </section>
);

SecurityBlock.propTypes = {
  params: shape({
    title: string,
    description: string,
    text: string
  })
};

export default SecurityBlock;



