import React from 'react';
import { arrayOf, string, shape } from 'prop-types';
import cx from 'classnames';

import { Skill } from '../../../components/Skill';
import './SkillsList.scss';

const SkillSet = ({ skills, image, isColumn, reversed }, index) => {
  const cl = cx({
    skillset__content: true,
    'skillset__content--columns': isColumn,
    'skillset__content--rows': !isColumn,
    'skillset__content--reversed': reversed
  });

  return (
    <div className='skillset__container' key={index}>
      <div className={cl}>
        <div className='skillset__image'>
          <img
            src={`${process.env.ASSETS_URL}${image}`}
            alt={image}/>
        </div>

        <div className='skillset__items'>
          {skills.map((item, i) => Skill(item, i))}
        </div>
      </div>
    </div>
  );
};

const SkillsList = ({ params }) => {
  return (
    <div className='skillsList__container'>
      {params.map((item, index) => SkillSet(item, index))}
    </div>
  );
};

SkillsList.propTypes = {
  params: arrayOf(shape({
    title: string,
    description: string,
    action: string,
    actionUrl: string,
    image: string
  }))
};

SkillsList.defaultProps = {
  params: []
};

export default SkillsList;



