import React from 'react';
import { string } from 'prop-types';

import './Skill.scss';

const Skill = ({
  title,
  icon,
  color,
  description
}, index) => {
  const st = { gridArea: `c${index}` };

  return (
    <div className='skill__container' key={index} style={st}>
      <div className='skill__header'>
        <i className='material-icons' style={{ color }}>
          {icon}
        </i>

        <h2 className='extra-small'>{title}</h2>
      </div>

      <p
        className='copytext-small'
        dangerouslySetInnerHTML={{ __html: description }}
      />
    </div>
  );
};

Skill.propTypes = {
  title: string,
  icon: string,
  color: string,
  description: string
};

export default Skill;



