import React from 'react';
import { string, shape } from 'prop-types';

import './TitleBlock.scss';

const TitleBlock = ({ params }) => (
  <section className='title-block__container'>
    <img
      src={`${process.env.ASSETS_URL}backgrounds/skills-title.png`}
      alt={'skills'}
    />

    <div className='title-block__content'>
      <h1>{params.title}</h1>

      {params.description
        .split('<br/>')
        .map((item, index) => (
          <p
            className='copytext'
            key={index}
            dangerouslySetInnerHTML={{ __html: item }}
          />
        ))}
    </div>
  </section>
);

TitleBlock.propTypes = {
  params: shape({
    title: string,
    description: string,
    inputPlaceholder: string,
    buttonText: string
  })
};

export default TitleBlock;



